import React, { useRef } from 'react';
import { useDateSegment } from 'react-aria';
import type { DateFieldState, DateSegment } from 'react-stately';
import classNames from 'classnames';

import { Text } from '../Typography';

import styles from './DateSegment.module.scss';

type DateSegmentProps = {
    segment: DateSegment;
    state: DateFieldState;
    isHovered: boolean;
    isFocused: boolean;
};

const DateSegment = ({
    segment,
    state,
    isFocused,
    isHovered,
}: DateSegmentProps) => {
    const ref = useRef(null);
    const { segmentProps } = useDateSegment(segment, state, ref);
    const { text, isPlaceholder } = segment;

    const classList = classNames(styles.datesegment, {
        [styles['datesegment--hovered']]: isHovered,
        [styles['datesegment--focused']]: isFocused,
        [styles['datesegment--filled']]: !isPlaceholder,
    });

    return (
        <div {...segmentProps} className={classList} ref={ref}>
            <Text
                variant="line"
                weight="400"
                className={styles.datesegment__text}
            >
                {text}
            </Text>
        </div>
    );
};

export default DateSegment;
