import React, { DOMAttributes, useRef } from 'react';
import { AriaDateFieldProps, useDateField, useLocale } from 'react-aria';
import { useDateFieldState } from 'react-stately';
import { createCalendar } from '@internationalized/date';
import { DateValue } from '@react-types/calendar';

import DateSegment from './DateSegment';

import styles from './DateField.module.scss';

interface DateFieldProps extends AriaDateFieldProps<DateValue> {
    classname?: string;
    hoverProps: DOMAttributes<HTMLDivElement>;
    focusWithinProps: DOMAttributes<HTMLDivElement>;
    isHovered: boolean;
    isFocused: boolean;
}

const DateField = (props: DateFieldProps) => {
    const ref = useRef(null);
    const { locale } = useLocale();
    const state = useDateFieldState({
        ...props,
        locale,
        createCalendar,
    });
    const { fieldProps } = useDateField(props, state, ref);

    return (
        <div
            className={styles.base}
            {...props.hoverProps}
            {...props.focusWithinProps}
        >
            <div {...fieldProps} ref={ref} className={styles.datefield}>
                {state.segments.map((segment, i) => (
                    <DateSegment
                        key={i}
                        segment={segment}
                        state={state}
                        isHovered={props.isHovered}
                        isFocused={props.isFocused}
                    />
                ))}
            </div>
        </div>
    );
};

export default DateField;
