import React, { useRef } from 'react';
import {
    AriaButtonProps,
    mergeProps,
    useButton,
    useFocusRing,
} from 'react-aria';
import classNames from 'classnames';

import styles from './DateFieldButton.module.scss';

const DateFieldButton = (props: AriaButtonProps) => {
    const { children } = props;
    const triggerRef = useRef(null);
    const { buttonProps, isPressed } = useButton(props, triggerRef);
    const { isFocusVisible, focusProps } = useFocusRing();

    const classList = classNames(styles.button, {
        [styles['button--pressed']]: isPressed,
        [styles['button--focused']]: isFocusVisible,
    });

    return (
        <button
            {...mergeProps(buttonProps, focusProps)}
            className={classList}
            ref={triggerRef}
        >
            <div className={styles.button__wrapper}>{children}</div>
        </button>
    );
};

export default DateFieldButton;
