import React from 'react';
import { AriaCalendarProps, useCalendar, useLocale } from 'react-aria';
import { useCalendarState } from 'react-stately';
import { createCalendar } from '@internationalized/date';
import type { DateValue } from '@react-types/calendar';

import { Button } from '../Button';
import SvgCaretLeft from '../Icons/CaretLeft';
import SvgCaretRight from '../Icons/CaretRight';
import { Text } from '../Typography';

import CalendarGrid from './CalendarGrid';

import styles from './Calendar.module.scss';

const Calendar = (props: AriaCalendarProps<DateValue>) => {
    const { locale } = useLocale();
    const state = useCalendarState({
        ...props,
        locale,
        createCalendar,
    });
    const { calendarProps, prevButtonProps, nextButtonProps, title } =
        useCalendar(props, state);

    return (
        <div {...calendarProps} className={styles.calendar}>
            <div className={styles.calendar__header}>
                <Button {...prevButtonProps} variant="tertiary" isIcon>
                    <SvgCaretLeft
                        aria-hidden="true"
                        className={styles.calendar__icon}
                    />
                </Button>
                <Text
                    variant="line"
                    size="small"
                    weight="600"
                    className={styles.calendar__title}
                >
                    {title}
                </Text>
                <Button {...nextButtonProps} variant="tertiary" isIcon>
                    <SvgCaretRight
                        aria-hidden="true"
                        className={styles.calendar__icon}
                    />
                </Button>
            </div>
            <CalendarGrid state={state} />
        </div>
    );
};

export default Calendar;
