import React from 'react';
import { AriaCalendarGridProps, useCalendarGrid, useLocale } from 'react-aria';
import { CalendarState } from 'react-stately';
import { getWeeksInMonth } from '@internationalized/date';

import { Text } from '../Typography';

import CalendarCell from './CalendarCell';

import styles from './CalendarGrid.module.scss';

interface CalendarGridProps extends AriaCalendarGridProps {
    state: CalendarState;
}

const CalendarGrid = ({ state, ...props }: CalendarGridProps) => {
    const { locale } = useLocale();
    const { gridProps, headerProps, weekDays } = useCalendarGrid(props, state);
    const weeksInMonth = getWeeksInMonth(state.visibleRange.start, locale);

    return (
        <table {...gridProps}>
            <thead {...headerProps} className={styles.header}>
                <tr className={styles.row}>
                    {weekDays.map((day, index) => (
                        <th key={index} className={styles.cell}>
                            <Text variant="line" size="small" weight="600">
                                {day}
                            </Text>
                        </th>
                    ))}
                </tr>
            </thead>
            <tbody className={styles.body}>
                {[...new Array(weeksInMonth).keys()].map((weekIndex) => (
                    <tr key={weekIndex} className={styles.row}>
                        {state
                            .getDatesInWeek(weekIndex)
                            .map((date, i) =>
                                date ? (
                                    <CalendarCell
                                        key={i}
                                        state={state}
                                        date={date}
                                    />
                                ) : (
                                    <td key={i} />
                                )
                            )}
                    </tr>
                ))}
            </tbody>
        </table>
    );
};

export default CalendarGrid;
