import React, { useRef } from 'react';
import {
    AriaCalendarCellProps,
    mergeProps,
    useCalendarCell,
    useFocusRing,
} from 'react-aria';
import { CalendarState } from 'react-stately';
import classNames from 'classnames';

import { Text } from '../Typography';

import styles from './CalendarCell.module.scss';

interface CalendarCellProps extends AriaCalendarCellProps {
    state: CalendarState;
    gaName?: string;
}

const CalendarCell = ({ state, date }: CalendarCellProps) => {
    const ref = useRef(null);
    const {
        cellProps,
        buttonProps,
        isSelected,
        isOutsideVisibleRange,
        isDisabled,
        isUnavailable,
        formattedDate,
    } = useCalendarCell({ date }, state, ref);
    const { isFocusVisible, focusProps } = useFocusRing();

    const classList = classNames(styles.cell, {
        [styles[`cell--selected`]]: isSelected,
        [styles[`cell--unavailable`]]: isUnavailable || isDisabled,
        [styles[`cell--focused`]]: isFocusVisible,
    });

    return (
        <td {...cellProps} className={styles.base}>
            <div
                {...mergeProps(buttonProps, focusProps)}
                className={classList}
                ref={ref}
                hidden={isOutsideVisibleRange}
            >
                <Text variant="line" weight="400" className={styles.cell__text}>
                    {formattedDate}
                </Text>
            </div>
        </td>
    );
};

export default CalendarCell;
