import React, { forwardRef, useRef, useState } from 'react';
import {
    AriaDatePickerProps,
    mergeProps,
    useDatePicker,
    useFocusRing,
    useFocusWithin,
    useHover,
    useInteractOutside,
} from 'react-aria';
import { useDatePickerState } from 'react-stately';
import { useObjectRef } from '@react-aria/utils';
import { DateValue } from '@react-types/calendar';
import classNames from 'classnames';

import Calendar from '../Calendar/Calendar';
import Dialog from '../Dialog/Dialog';
import { SvgCalendar } from '../Icons';
import Popover from '../Popover/Popover';
import { Stack } from '../Stack';
import { DescriptionSeverity } from '../types';
import { Label, Text } from '../Typography';

import DateField from './DateField';
import DateFieldButton from './DateFieldButton';

import styles from './DatePicker.module.scss';

export interface DatePickerProps extends AriaDatePickerProps<DateValue> {
    errorMessage?: string;
    descriptionSeverity?: DescriptionSeverity;
    labelSuffix?: string;
    tooltipTrigger?: React.ReactNode;
}

const DatePicker = forwardRef<HTMLDivElement, DatePickerProps>((props, ref) => {
    const forwardedRef = useObjectRef(ref);
    const fallbackRef = useRef(null);
    const triggerRef = forwardedRef || fallbackRef;
    const popoverRef = useRef(null);

    const state = useDatePickerState(props);
    const {
        groupProps,
        labelProps,
        fieldProps,
        buttonProps,
        dialogProps,
        calendarProps,
        descriptionProps,
        errorMessageProps,
        validationErrors,
        isInvalid,
    } = useDatePicker(props, state, triggerRef);

    const { isFocusVisible, focusProps } = useFocusRing({
        isTextInput: true,
        within: true,
    });
    const { hoverProps, isHovered } = useHover({});

    const [isFocusWithin, setFocusWithin] = useState(false);
    const { focusWithinProps } = useFocusWithin({
        onFocusWithinChange: (isFocusWithin) => setFocusWithin(isFocusWithin),
    });

    useInteractOutside({
        ref: popoverRef,
        onInteractOutside: state.close,
    });

    const {
        label,
        description,
        validationBehavior,
        errorMessage: customErrorMessage,
        descriptionSeverity = 'regular',
        labelSuffix,
        tooltipTrigger,
    } = props;
    const { isOpen, value } = state;

    const classList = classNames(styles.datepicker, {
        [styles['datepicker--hovered']]: isHovered,
        [styles['datepicker--focused']]: isFocusVisible,
        [styles['datepicker--focus-within']]: isFocusWithin,
        [styles['datepicker--active']]: isOpen,
        [styles['datepicker--filled']]: value,
        [styles['datepicker--invalid']]: isInvalid,
    });

    const descriptionClassList = classNames({
        [styles['messages__description--regular']]:
            descriptionSeverity === 'regular',
        [styles['messages__description--warning']]:
            descriptionSeverity === 'warning',
    });

    // Allow only native error messages with native validation behaviour
    const errorMessage =
        validationBehavior === 'native'
            ? validationErrors.join(' ')
            : customErrorMessage;

    const hasMessages = errorMessage || description;

    return (
        <>
            <Stack>
                {label && (
                    <div className={styles.label__container}>
                        <Label
                            weight="600"
                            className={styles.label}
                            {...labelProps}
                        >
                            {label}
                            {labelSuffix && (
                                <>
                                    {/* Use an extra element as separator so everything stays inline and no extra margin on either side needed, which doesn't look right when text starts wrapping */}
                                    <span
                                        className={
                                            styles['label__suffix-separator']
                                        }
                                    ></span>
                                    <Text
                                        variant="line"
                                        italic
                                        size="small"
                                        className={styles.label__suffix}
                                    >
                                        {labelSuffix}
                                    </Text>
                                </>
                            )}
                        </Label>
                        {tooltipTrigger}
                    </div>
                )}

                <div {...groupProps} className={classList} ref={triggerRef}>
                    <DateField
                        {...mergeProps(fieldProps, focusProps)}
                        hoverProps={hoverProps}
                        focusWithinProps={focusWithinProps}
                        isHovered={isHovered}
                        isFocused={isFocusWithin}
                    />

                    <DateFieldButton {...buttonProps}>
                        <SvgCalendar
                            className={styles.datepicker__icon}
                            aria-hidden="true"
                        />
                    </DateFieldButton>
                </div>

                {hasMessages && (
                    <Stack>
                        {/* Show custom error message only when isInvalid */}
                        {isInvalid && errorMessage && (
                            <div
                                {...errorMessageProps}
                                className={styles.messages__error}
                            >
                                <Text variant="line" size="small">
                                    {errorMessage}
                                </Text>
                            </div>
                        )}
                        {description && (
                            <div
                                {...descriptionProps}
                                className={descriptionClassList}
                            >
                                <Text variant="line" size="small">
                                    {description}
                                </Text>
                            </div>
                        )}
                    </Stack>
                )}
            </Stack>

            {isOpen && (
                <Popover
                    popoverRef={popoverRef}
                    state={state}
                    triggerRef={triggerRef}
                    placement="bottom end"
                    offset={6}
                    className={styles.popover}
                    isNonModal
                >
                    <Dialog {...dialogProps}>
                        <Calendar {...calendarProps} />
                    </Dialog>
                </Popover>
            )}
        </>
    );
});

DatePicker.displayName = 'DatePicker';

export default DatePicker;
